// app/@modal/(.)notes/[id]/page.tsx

// НЕ ПОТРІБЕН 'use client', це серверний компонент
import ModalNotePreviewClient from './NotePreview.client';

interface NoteModalPageProps {
  // `params` тут не Promise, оскільки це @-маршрут (згідно з документацією)
  params: {
    id: string;
  };
}

export default function NoteModalPage({ params }: NoteModalPageProps) {
  const { id } = params;

  // Просто рендеримо клієнтський компонент і передаємо йому ID
  return <ModalNotePreviewClient id={id} />;
}